<template>
    <div>
        <el-row :gutter="10"style="background-color:rgb(238, 238, 238);padding-top: 50px;padding-bottom: 50px;">
            <el-col :lg="10" :md="10" :sm="10" :xs="10" :offset="7" style="background-color:#fff;padding:30px 25px 15px 0">
                <el-form :model="trainForm" :rules="trainRules" ref="trainForm" label-width="100px" class="demo-ruleForm">

                    <el-form-item label="" prop="name">
                        <el-input v-model="trainForm.name" placeholder=""></el-input>
                    </el-form-item>

                    <el-form-item label="" prop="phone">
                        <el-input v-model="trainForm.phone" placeholder=""></el-input>
                    </el-form-item>

                    <el-form-item>
                        <el-button @click="submitForm('trainForm')" style="background-color: #f48100;border:none;color:#fff;"></el-button>
                    </el-form-item>
                </el-form>
            </el-col>
        </el-row>
    </div>
</template>

<script>
	import api from "../axios/api.js";
	export default {
		data() {
			//
			let validatePhone = (rule, value, callback) => {
				let re = /^1[34578]\d{9}$/;
				if (value === "" || !re.test(value) || value.length < 11) {
					callback(new Error(""));
				} else {
					callback();
				}
			};
			//
			let validateName = (rule, value, callback) => {
				let re = /^[\u4E00-\u9FA5\uf900-\ufa2d]{2,5}$/;
				if (value === "" || !re.test(value) || value.length < 2 || value.length > 5) {
					callback(new Error(""));
				} else {
					callback();
				}
			};
			return {
				trainForm: {
					phone: '',
					name: ''
				},
				trainRules: {
					phone: [{
						required: true,
						validator: validatePhone,
						message: '',
						trigger: 'blur'
					}],
					name: [{
						required: true,
						validator: validateName,
						message: '',
						trigger: 'blur'
					}],
				}
			}
		},
		methods: {
			submitForm(formName) {
				this.$refs[formName].validate((valid) => {
					if (valid) {
						var params = {
							"phone": this.trainForm.phone,
							"name": this.trainForm.name
						};
						api.Post('/activity/apply', params)
							.then(res => {
								console.log(res);
								if(res['msg'] == null) {
									alert("");
								}
							});
					} else {
						console.log('error submit!!');
						return false;
					}
				});
			}
		}
	}


</script>

<style>

</style>


