<template>
    <div>
        <el-row :gutter="10" style="background-color:rgb(238, 238, 238);padding-top: 50px;padding-bottom: 50px;">
            <el-col :lg="14" :md="10" :sm="10" :xs="10" :offset="5" style="background-color:#fff;padding:30px 25px 15px 10px">
                <el-form :model="tenancyForm" :rules="tenancyRules" ref="tenancyForm" label-width="100px" class="tenancyForm">
                    <!-- <h2></h2> -->
                    <el-form-item label="" prop="name">
                        <el-input v-model="tenancyForm.name"></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="phone">
                        <el-input v-model="tenancyForm.phone"></el-input>
                    </el-form-item>

                    <el-form-item>
                        <el-button type="primary" @click="submitForm('ruleForm')"></el-button>
                    </el-form-item>

                </el-form>
            </el-col>
        </el-row>
    </div>
</template>

<script>
	export default {
		data() {
			return {
				tenancyForm: {
					name: '',
					phone: '',
				
				},
				tenancyRules: {
					name: [
						{ required: true, message: '', trigger: 'blur' },
						{ required: true, message: '', trigger: 'blur' }
					],
					region: [
						{ required: true, message: '', trigger: 'blur' }
					],
					
				}
			};
		},
		methods: {
			submitForm(formName) {
				this.$refs[formName].validate((valid) => {
					if (valid) {
						
					} else {
						console.log('error submit!!');
						return false;
					}
				});
			},
			resetForm(formName) {
				this.$refs[formName].resetFields();
			}
		}
	}
</script>
<style>
   .tenancyForm {
       width: 440px;
   }
    h2{
        color:#20a0ff;
        font-size: 16px;
        font-weight: 600;
        margin:20px 10px;
    }
    .line{
        text-align: center;
    }
</style>


