<template>
    <div class="login">
        <div class="container">
            <div class="login_wrap">
                <el-row>
                    <el-col :xs="24" :sm="24" :md="24" :lg="24">
                        <el-form label-width="90px" class="login_form" :model="loginForm" :rules="loginRules" ref="loginForm">
                            <el-form-item class="tc f18 b"></el-form-item>
                            <el-form-item label=":" prop="phone">
                                <el-input type="tel" v-model="loginForm.phone" auto-complete="off" placeholder=""></el-input>
                            </el-form-item>
                            <el-form-item label=":" prop="pass">
                                <el-input type="password" v-model="loginForm.pass" auto-complete="off" placeholder="6-16"></el-input>
                            </el-form-item>
                            <el-form-item class="loginBtn">
                                <el-button type="primary" @click="login('loginForm')"></el-button>
                                <router-link to="/register" class="r f12"></router-link>
                                <router-link to="/forgetPassword" class="r f12">&nbsp;&nbsp;|&nbsp;&nbsp;</router-link>
                            </el-form-item>
                        </el-form>
                    </el-col>
                </el-row>
            </div>
        </div>
    </div>
</template>

<script>
    import api from '../../axios/api.js'
    export default {
        data() {
            // 
            let validatePhone = (rule, value, callback) => {
                let re = /^1[34578]\d{9}$/;
                if (value === "" || !re.test(value) || value.length < 11) {
                    callback(new Error(""));
                } else {
                    callback();
                }
            };
            let validatePass = (rule, value, callback) => {
                let re = /^[A-Za-z0-9\!\@\#\$\%\^\&\*\(\)\_\+\`\~\-\=\,\.\;\:\<\>\?\|]{6,16}$/;
                if (value === "" || !re.test(value) || value.length < 6 || value.length > 16) {
                    callback(new Error("6-16"));
                } else {
                    callback();
                }
            };
            return {
                loginForm: {
                    phone: "",
                    pass: ""
                },
                loginRules: {
                    phone: [{
                        required: true,
                        validator: validatePhone,
                        message: '',
                        trigger: 'blur'
                    }],
                    pass: [{
                        required: true,
                        validator: validatePass,
                        trigger: "blur"
                    }],
                },
            }
        },
        methods: {
            login(formName) {
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        var params = {
                            "phone": this.loginForm.phone,
                            "password": this.loginForm.pass
                        }
                        api.Post('/pub/sign/in', params)
                            .then(res => {
                                if (res['suc'] == true) {
                                    var expireDate = new Date();
                                    expireDate.setDate(expireDate.getDate() + 1);
                                    window.localStorage.setItem('Authorization',res['msg']);//
                                    let userCookie = JSON.stringify(res);    
                                    api.SetCookie("userInfo", userCookie, expireDate);
                                    this.$router.push({ path: 'index' }) 
                                } else if(res['suc'] == false){
                                    alert(res['msg']);
                                }
                            });
                    } else {
                        console.log('error submit!!');
                        return false;
                    }
                });
            },
        }
    }
</script>



